export default {
	props: {
		size: {
			type: String
		},
		min: {
			type: [Number, String],
			default: 1
		},
		max: {
			type: [Number, String],
			default: Infinity
		},
		defaultValue: {
			type: [Number, String],
			default: 1
		},
		display: {
			type: [Boolean, String],
			default: false
		}
	},
	data() {
		const value = this.defaultValue;
		return {
			currentValue: value
		};
	},
	watch: {
		defaultValue(newValue) {
			this.currentValue = newValue;
		},
		currentValue(newValue) {
			this.$emit('change', this.currentValue);
		}
	},
	computed: {
		minusDisabled() {
			return this.display || this.currentValue <= this.min;
		},

		plusDisabled() {
			return this.display || this.currentValue >= this.max;
		}
	},
	methods: {
		format(value) {
			value = String(value).replace(/[^0-9.-]/g, '');
			return value === '' ? 1 : Math.floor(value);
		},
		onBlur(event){
			let value = event.detail.value;
			this.currentValue = this.format(value) > this.max ? parseInt(this.max) : this.format(value);
		},
		onInput(event) {
			// let value = event.detail.value;
			// setTimeout(() => {
			// 	this.currentValue = this.format(value) > this.max ? parseInt(this.max) : this.format(value);
			// }, 0);
		},
		reduce() {
			if (!this.minusDisabled) {
				this.currentValue--;
			}
		},
		add() {
			if (!this.plusDisabled) {
				this.currentValue++;
			}
		}
	}
};
